export default {
	data() {
		return {
			yyzz:'',
			khxuz:'',
			gsmp:''
		}
	},
	methods: {
		getImg(){
			console.log("tap");
		},
		selectImg(type){
			var _this = this;
			uni.chooseImage({
				count:1,
				success(res) {
					console.log(res);
					var img = res.tempFilePaths[0];
					if(type=="yyzz"){
						_this.yyzz = img;
					} else if(type=="khxuz"){
						_this.khxuz = img;
					} else if(type=="gsmp"){
						_this.gsmp = img;
					}
				}
			})
		},
		operator(id) {
			var _this = this;
			uni.showActionSheet({
				itemList: ["删除图片"],
				success: function(res){
					// 下标为0则下载
					if (res.tapIndex == 0) {
						if(id=='yyzz'){
							_this.yyzz = '';
						}  else if(id=="khxuz"){
							_this.khxuz = '';
						} else if(id=="gsmp"){
							_this.gsmp = '';
						}
					}
				}
			})
		}
	}
}
